.no-webp .inicio{
    background-image: url(../img/header5.jpg);
}
.webp .inicio {
    background-image: url(../img/header5.webp);
}
.header {
    background-color: $grisOscuro;
    padding: 1rem 0 3rem 0;
    
    &.inicio {
        background-position: center center;
        background-size: cover;
        @include tablet{
            min-height: 70rem;
        }
        .contenido-header{
        @include tablet{
                    min-height: 70rem;
                }
            }
    }
     &.fijo {
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .1rem .2rem .3rem #333;
            background-color: rgba(54, 47, 47, 0.5);
            border-radius: 0 0 1rem 1rem;
        }
}

.contenido-header{
    display: flex;
    flex-direction: column;
    justify-content: space-between;

  
    h1 {
        text-align: left;
        color: $blanco;
        font-weight: $bold;
        line-height: 1;
        max-width: 60rem;
    }
}
.barra {
    img{
        width: 12rem;
        height: 4rem;
    }

}