.dark-mode {
  background-color: #1a1a1a;
  /* Estilo para el cuadro de búsqueda */
  /* Estilo para el label asociado al cuadro de búsqueda */
  /* Estilo para los botones de paginación de DataTables */
  /* Estilo para el texto de la información de DataTables (ejemplo: "Showing 1 to 10 of 20 entries") */
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode blockquote {
  color: #FFFFFF;
}
.dark-mode .anuncio,
.dark-mode .resumen-propiedad {
  background-color: #333333;
  border: 2rem solid #333333;
}
.dark-mode .anuncio .precio,
.dark-mode .resumen-propiedad .precio {
  color: #71B100;
}
.dark-mode .anuncio {
  /*se obtuvo el codigo de la pagina cssmatic */
  -webkit-box-shadow: 0px 11px 15px -9px rgba(165, 158, 158, 0.63);
  -moz-box-shadow: 0px 11px 15px -9px rgba(156, 140, 140, 0.63);
  box-shadow: 0px 11px 15px -9px rgba(208, 204, 204, 0.63);
}
.dark-mode .icono {
  filter: invert(100%);
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  color: #FFFFFF;
  border: 1px solid #333333;
}
.dark-mode .tabla-container {
  color: #FFFFFF;
}
.dark-mode table.propiedades {
  width: 100%;
  border-collapse: collapse;
  background-color: #333333;
}
.dark-mode table.propiedades td {
  background-color: #333333;
  color: #FFFFFF;
}
.dark-mode .dataTables_filter input {
  font-weight: 700 !important;
  margin-bottom: 2rem;
  color: 900 !important;
  background-color: #fff !important;
  border: 1px solid #ccc !important;
}
.dark-mode .dataTables_filter label {
  font-weight: 700 !important;
  margin-bottom: 2rem;
  color: #ffffff !important; /* Color del texto del label */
}
.dark-mode .dataTables_paginate .paginate_button {
  font-weight: 700 !important;
  color: #ffffff !important; /* Color del texto de los botones de paginación */
  background-color: #b5ddb5 !important; /* Color de fondo de los botones de paginación */
  border: 1px solid #ccc !important; /* Borde de los botones de paginación */
  border-radius: 1rem !important;
  cursor: pointer !important;
}
.dark-mode .dataTables_info {
  font-weight: 700 !important;
  color: #ffffff !important; /* Color del texto de la información */
}

.tabla-container {
  display: block;
  margin: 0 auto;
  width: 80%;
  overflow-x: auto; /* Habilita el desplazamiento horizontal */
  overflow-y: auto; /* Habilita el desplazamiento vertical */
  max-height: auto; /* Altura máxima del contenedor antes de que aparezca la barra de desplazamiento vertical */
}

.table {
  width: 100%;
  border-collapse: collapse;
  border: 1px solid #ddd; /* Borde alrededor de la tabla */
}

.table th, .table td {
  padding: 8px;
  text-align: left;
  border: 1px solid #ddd; /* Borde alrededor de las celdas */
}

/* Estilo para los botones de paginación de DataTables */
.dataTables_paginate .paginate_button {
  font-weight: 700 !important;
  color: #ffffff !important; /* Color del texto de los botones de paginación */
  background-color: #FFFFFF !important; /* Color de fondo de los botones de paginación */
  border: 3px solid #ccc !important; /* Borde de los botones de paginación */
  border-radius: 1rem !important;
  cursor: pointer !important;
}

/* Estilo para el cuadro de búsqueda */
.dataTables_filter input {
  font-weight: 700 !important;
  margin-bottom: 2rem;
}

/* Estilo para el label asociado al cuadro de búsqueda */
.dataTables_filter label {
  font-weight: 700 !important;
  margin-bottom: 2rem;
}

/* Estilo para el texto de la información de DataTables (ejemplo: "Showing 1 to 10 of 20 entries") */
.dataTables_info {
  font-weight: 700 !important;
}

/** UI**/
.no-webp .inicio {
  background-image: url(../img/header5.jpg);
}

.webp .inicio {
  background-image: url(../img/header5.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}
.header.fijo {
  left: 0;
  top: 0;
  position: fixed;
  width: 100%;
  box-shadow: 0.1rem 0.2rem 0.3rem #333;
  background-color: rgba(54, 47, 47, 0.5);
  border-radius: 0 0 1rem 1rem;
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 1;
  max-width: 60rem;
}

.barra img {
  width: 12rem;
  height: 4rem;
}

.footer {
  background-color: #333333;
  padding: 1rem 0;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 3rem;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 4rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  line-height: 2;
  font-weight: 700;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 2rem;
  }
  .navegacion alast-of-type {
    margin-right: 0;
  }
}
.navegacion a:hover {
  color: #e1e1e1;
  font-size: 2.5rem;
  font-weight: 700;
}

/**Menu de Hamburguesa**/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.iconos-nosotros {
  text-transform: uppercase;
  text-align: center;
  place-items: center;
  display: grid;
  gap: 2.5rem;
  grid-template-columns: repeat(3, 1fr);
}
@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
  }
}

.iconos-contacto {
  text-transform: uppercase;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  gap: 1rem;
}
@media (min-width: 768px) {
  .iconos-contacto {
    gap: 10rem;
  }
}
.iconos-contacto .iconos img {
  height: 5rem;
  width: 5rem;
}
.iconos-contacto .iconos h3 {
  text-transform: uppercase;
}

.iconos {
  text-align: justify;
}
.iconos img {
  height: 8rem;
  width: 8rem;
}
.iconos h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.p-center p {
  text-align: center;
  font-size: 3rem;
}

.heading-bold {
  font-weight: 700;
  text-align: center;
  font-size: 3rem;
}

.anuncio {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  margin-bottom: 2rem;
  border-radius: 2rem;
  /*se obtuvo el codigo de la pagina cssmatic */
