}

.no-webp.imagen-contacto {
  background-image: url(../img/header8.jpg);
}

.webp .imagen-contacto {
  background-image: url(../img/header8.webp);
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  color: #FFFFFF;
  font-size: 4rem;
  font-weight: 900;
}
.imagen-contacto p {
  font-size: 2rem;
  color: #FFFFFF;
  font-weight: 700;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.informacion-meta span {
  color: #E08709;
}

.texto-entrada a {
  color: #000000;
  text-decoration: none;
  margin-top: 2rem;
  display: block;
}
