.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #e1e1e1;
  border: 1px solid #FFFFFF;
  border-radius: 1rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2rem;
  margin: 3rem 0 0 0;
  padding-left: 1rem 0 3rem 0;
}

.alerta {
  display: block;
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem auto;
  width: 70%;
  border-radius: 1rem;
}
.alerta.error {
  background-color: rgb(157, 17, 17);
}
.alerta.exito {
  background-color: #71B100;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #140047;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.propiedades .imagen-tabla {
  width: 10rem;
}

.imagen-small {
  width: 20rem;
}