.derecha{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0;
    
    @include tablet {
        align-items: flex-end;
    }

    .dark-mode-boton{
        width: 4rem;
        margin-top: 2rem;
        margin-bottom:2rem;
       filter: invert(100%);

        &:hover {
            cursor: pointer;
        }

        @include tablet {
            margin-top: 0;
            

            
        }
       

    }
}
.barra {
    display: flex;

    flex-direction: column;
    padding-top: 3rem;
    align-items: center;
    @include tablet {
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;

    }
}
.navegacion {

    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .3s ease-in-out, opacity 0.5s linear, height .5s linear;
  
    @include tablet {
        display: flex;
        align-items: center;
        opacity: 1;
        visibility: visible;
    }
    a {
        color: $blanco;
        line-height: 2;
        font-weight: $bold;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;
       
    

     @include tablet {
        margin-right: 2rem;
        font-size:2rem ;

        &last-of-type {
            margin-right: 0;
        }
     }
     &:hover{
        color: $gris;
        font-size: 2.5rem;
        font-weight: $bold;
     }
    }
}

/**Menu de Hamburguesa**/

.mobile-menu{
    @include tablet {
        display: none;
    }

    img{
        width: 5rem;
    }
}

.mostrar{
 visibility: visible;
 opacity: 1;
 height: auto;
}